:host {
  display: block;
}

login-form form {
  width: 400px;
}
login-form label {
  position: absolute;
  transform: translateY(6px);
  left: 13px;
  transition: all 0.25s ease;
  pointer-events: none;
  font-size: 22px;
}
login-form label .req {
  margin: 2px;
  color: green;
}
login-form label.active {
  left: 2px;
  font-size: 14px;
}
login-form label.active .req {
  opacity: 0;
}
login-form label.highlight {
  color: #fff;
}
login-form input {
  font-size: 22px;
  display: block;
  padding-left: 130px;
  padding-bottom: 8px;
  padding-top: 8px;
  border: 1px solid lightgray;
  border-radius: 0;
}
login-form input:focus {
  outline: 0;
  border-color: green;
}
login-form .field-wrap {
  position: relative;
  margin-bottom: 20px;
}
login-form button {
  border-radius: 5px;
  font-weight: bold;
  text-transform: uppercase;
  background: #000;
  color: #fff;
  transition: all 0.5s ease;
  width: 100px;
  height: 30px;
}
login-form button:hover, login-form button:focus {
  background: lightgrey;
}
login-form .forgot {
  margin-top: 20px;
  text-align: right;
}